html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: local("Montserrat-Medium"), local("Montserrat-Medium"), url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-style: italic;
  font-weight: 500;
  src: local("Montserrat-MediumItalic"), local("Montserrat-MediumItalic"), url("../fonts/Montserrat-MediumItalic.woff2") format("woff2"), url("../fonts/Montserrat-MediumItalic.woff") format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: local("Montserrat-SemiBold"), local("Montserrat-SemiBold"), url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-style: italic;
  font-weight: 600;
  src: local("Montserrat-SemiBold"), local("Montserrat-SemiBoldItalic"), url("../fonts/Montserrat-SemiBoldItalic.woff2") format("woff2"), url("../fonts/Montserrat-SemiBoldItalic.woff") format("woff");
}

.icon-arrow {
  width: 1em;
  height: 1em;
  fill: currentColor;
}

.icon-battery {
  width: 1.96em;
  height: 1em;
  fill: currentColor;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: currentColor;
}

body {
  background: #f7f7f7;
  color: rgba(35, 31, 32, 0.5);
  font-family: "Montserrat", Helvetica, sans-serif;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.667;
}

a {
  text-decoration: none;
  color: inherit;
}

.inner {
  max-width: 1030px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.wrapper {
  background-color: #fff;
  text-align: center;
}

.block {
  display: flex;
}

.title {
  color: #231f20;
  font-family: "Montserrat", Helvetica, sans-serif;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.533;
}

.title-lg {
  color: #f7f7f7;
  font-family: "Montserrat", Helvetica, sans-serif;
  font-size: 110px;
  font-weight: 600;
}

.text-block {
  max-width: 530px;
  margin: 0 auto;
}

.header {
  background-color: #fff;
  padding: 12px 0;
}

.header__wrapper {
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
}

.main-nav__list {
  display: flex;
  align-items: center;
  margin: 0 -30px;
}

.main-nav__item {
  padding: 0 30px;
}

.main-nav__link {
  color: #231f20;
  font-size: 10px;
  line-height: 1.2;
}

.logo img {
  display: block;
  max-width: 100%;
}

.hero__wrapper {
  margin-bottom: 10px;
  background-color: #fff;
  position: relative;
  display: flex;
  align-items: stretch;
}

@media only screen and (max-width: 767px) {
  .hero__wrapper {
    flex-direction: column;
    background-color: transparent;
  }
}

.hero__left {
  flex: 0 0 42.7%;
  min-width: 42.7%;
  position: relative;
  z-index: 5;
}

@media only screen and (max-width: 767px) {
  .hero__left {
    flex: 0 0 auto;
    margin: -30px auto 0;
    min-width: 220px;
    width: 100%;
    max-width: 370px;
    padding: 0 50px;
  }
}

.hero__right {
  flex: 0 0 57.3%;
  min-width: 57.3%;
}

@media only screen and (max-width: 767px) {
  .hero__right {
    order: -1;
  }
}

.hero__right-bg {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  padding-bottom: 69.8%;
}

@media only screen and (max-width: 767px) {
  .hero__right-bg {
    padding-bottom: 50%;
  }
}

.hero__left-bg {
  height: 100%;
  width: 100%;
  background-size: 60%;
  background-position: 64px 44px;
  background-repeat: no-repeat;
}

@media only screen and (max-width: 767px) {
  .hero__left-bg {
    padding-bottom: 90%;
    background-color: #fff;
    background-position: center;
  }
}

.hero__center {
  position: absolute;
  bottom: 0;
  left: 213px;
  background-color: #fff;
  width: 427px;
  padding: 51px 30px 26px 65px;
  z-index: 1;
}

@media only screen and (max-width: 1023px) {
  .hero__center {
    padding-top: 31px;
  }
}

@media only screen and (max-width: 767px) {
  .hero__center {
    position: static;
    padding: 130px 20px 20px;
    text-align: center;
    width: 100%;
    margin-top: -120px;
  }
}

.hero__title {
  margin-bottom: 8px;
  color: #231f20;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.3;
}

[data-tabs] {
  overflow: hidden;
}

[data-tabs] [data-tabs-control] {
  cursor: pointer;
}

[data-tabs] [data-tabs-wrapper] {
  position: relative;
}

[data-tabs] [data-tabs-container] {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
}

[data-tabs] [data-tabs-container].is-open {
  position: relative;
  opacity: 1;
  visibility: visible;
}

[data-tabs-current] {
  display: none;
}

.tabs__list {
  display: flex;
  flex-direction: column;
}

.tabs_bg .tabs__list {
  flex-direction: row;
  justify-content: center;
}

@media only screen and (max-width: 767px) {
  .tabs__list {
    flex-direction: row;
    justify-content: space-around;
  }
  .tabs_bg .tabs__list {
    flex-direction: column;
  }
}

.tabs__item {
  display: block;
  color: #924592;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.533;
}

.tabs_bg .tabs__item {
  color: #231f20;
  margin: 0 6px;
}

.tabs__item:not(:last-child) {
  margin-bottom: 18px;
}

.tabs_bg .tabs__item:not(:last-child) {
  margin-bottom: 0;
}

@media only screen and (max-width: 767px) {
  .tabs_bg .tabs__item:not(:last-child) {
    margin-bottom: 10px;
  }
}

.tabs__item.is-active span:after {
  opacity: 1;
  visibility: visible;
}

.tabs_bg .tabs__item.is-active {
  background-image: linear-gradient(82deg, #1e91d0 0%, #5853a3 48%, #a44298 100%);
  color: #fff;
}

.tabs__item span {
  display: inline-block;
  position: relative;
}

.tabs__item span:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  height: 2px;
  width: 100%;
  background-color: #924592;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.4s, visibility 0.4s;
}

.tabs_bg .tabs__item span:after {
  display: none;
}

.tabs_bg .tabs__item {
  padding: 10px 15px 8px 15px;
  background-color: #f7f7f7;
}

.tabs__wrapper {
  max-width: 518px;
  margin: 0 auto;
  margin-top: 30px;
}

.wearables {
  margin-bottom: 174px;
}

.wearables__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-between;
  min-height: 447px;
  background-color: #fff;
}

@media only screen and (max-width: 767px) {
  .wearables__wrapper {
    max-height: inherit;
  }
}

.wearables__images {
  position: absolute !important;
  right: 0;
  left: 39.9%;
  height: 100%;
  min-height: 400px;
  max-width: 64.1%;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 767px) {
  .wearables__images {
    position: static !important;
    max-width: inherit;
    min-height: inherit;
  }
}

.wearables__image {
  padding: 30px 120px 30px 150px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 767px) {
  .wearables__image {
    padding: 15px;
  }
}

.wearables__image img {
  display: block;
  max-width: 100%;
  max-height: 390px;
  object-fit: cover;
}

@media only screen and (max-width: 767px) {
  .wearables__image img {
    max-height: 200px;
  }
}

.wearables__texts, .wearables__tabs, .wearables__info-wrapper {
  width: 39.9%;
}

@media only screen and (max-width: 767px) {
  .wearables__texts, .wearables__tabs, .wearables__info-wrapper {
    width: 100%;
  }
}

.wearables__texts {
  display: flex;
  height: 180px;
  overflow: auto;
}

@media only screen and (max-width: 767px) {
  .wearables__texts {
    height: auto;
  }
}

.wearables__text {
  padding: 30px 68px 10px 33px;
}

.wearables__tabs {
  padding-left: 33px;
}

.wearables__info-wrapper {
  background-image: linear-gradient(82deg, #1e91d0 0%, #5853a3 48%, #a44298 100%);
}

@media only screen and (max-width: 767px) {
  .wearables__info-wrapper {
    max-height: 83px;
  }
}

.wearables__info {
  padding: 44px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media only screen and (max-width: 767px) {
  .wearables__info {
    padding: 30px;
  }
}

.wearables__info span {
  color: #fff;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.533;
  display: inline-block;
  vertical-align: middle;
}

.wearables__info .icon {
  font-size: 23px;
  color: #fff;
  display: inline-block;
  vertical-align: middle;
}

.prepaid {
  margin-bottom: 106px;
}

.prepaid__wrapper {
  background-color: #fff;
  text-align: center;
  padding-top: 190px;
  padding-bottom: 60px;
}

.prepaid__cards {
  text-align: center;
  position: relative;
  top: -400px;
}

.parallax-card {
  display: inline-block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.parallax-card:nth-child(1) {
  top: 66px;
  z-index: 9;
}

.parallax-card:nth-child(2) {
  top: 132px;
  z-index: 8;
}

.parallax-card:nth-child(3) {
  top: 198px;
  z-index: 7;
}

.parallax-card:nth-child(4) {
  top: 264px;
  z-index: 6;
}

.parallax-card img {
  max-width: 343px;
  display: inline-block;
  filter: drop-shadow(0 20px 20px rgba(0, 0, 0, 0.6));
}

.luxury__wrapper {
  background-color: #fff;
  text-align: center;
  position: relative;
  padding-top: 170px;
  padding-bottom: 70px;
}

.luxury__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  text-align: center;
}

.luxury__image:after {
  content: '';
  position: absolute;
  top: 40px;
  left: 50%;
  transform: translateX(-50%);
  height: 2px;
  width: 200px;
  box-shadow: 0 110px 100px 60px rgba(0, 0, 0, 0.3);
}

.luxury__image img {
  margin-top: -66px;
  max-width: 394px;
}

.luxury__subtitle {
  margin-bottom: 8px;
}

.solutions__wrapper {
  background-color: #fff;
  text-align: center;
}

.solutions__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
}

.solutions-cta {
  position: relative;
  padding: 30px;
}

.solutions-cta__img {
  display: block;
}

.solutions-cta__arrow {
  position: absolute;
  transform-origin: center;
  font-size: 0;
  opacity: 0;
  transition: transform 0.4s, opacity 0.4s;
}

.is-animate .solutions-cta__arrow {
  opacity: 1;
}

.solutions-cta__arrow:nth-child(1) {
  top: 0;
  left: 0;
  transform: translate(-40px, -40px) rotate(45deg);
}

.is-animate .solutions-cta__arrow:nth-child(1) {
  transform: translate(0, 0) rotate(45deg);
}

.solutions-cta__arrow:nth-child(2) {
  top: 0;
  right: 0;
  transform: translate(40px, -40px) rotate(135deg);
}

.is-animate .solutions-cta__arrow:nth-child(2) {
  transform: translate(0, 0) rotate(135deg);
}

.solutions-cta__arrow:nth-child(3) {
  bottom: 0;
  right: 0;
  transform: translate(40px, 40px) rotate(225deg);
}

.is-animate .solutions-cta__arrow:nth-child(3) {
  transform: translate(0, 0) rotate(225deg);
}

.solutions-cta__arrow:nth-child(4) {
  bottom: 0;
  left: 0;
  transform: translate(-40px, 40px) rotate(-45deg);
}

.is-animate .solutions-cta__arrow:nth-child(4) {
  transform: translate(0, 0) rotate(-45deg);
}

.solutions-cta__arrow .icon {
  font-size: 30px;
}

.clients__logos {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.clients__logo img {
  max-width: 100%;
  display: block;
}
