.wearables {
  margin-bottom: 174px;
  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    min-height: 447px;
    background-color: $white;
    @include sm {
      max-height: inherit; } }
  &__images {
    position: absolute !important;
    right: 0;
    left: 39.9%;
    height: 100%;
    min-height: 400px;
    max-width: 64.1%;
    display: flex;
    align-items: center;
    justify-content: center;
    @include sm {
      position: static !important;
      max-width: inherit;
      min-height: inherit; } }
  &__image {
    padding: 30px 120px 30px 150px;
    @include coverdiv;
    display: flex;
    align-items: center;
    justify-content: center;
    @include sm {
      padding: 15px; }
    img {
      display: block;
      max-width: 100%;
      max-height: 390px;
      object-fit: cover;
      @include sm {
        max-height: 200px; } } }
  &__texts,
  &__tabs,
  &__info-wrapper {
    width: 39.9%;
    @include sm {
      width: 100%; } }
  &__texts {
    display: flex;
    height: 180px;
    overflow: auto;
    @include sm {
      height: auto; } }
  &__text {
    padding: 30px 68px 10px 33px; }
  &__tabs {
    padding-left: 33px; }
  &__info-wrapper {
    background-image: $grad;
    @include sm {
      max-height: 83px; } }
  &__info {
    padding: 44px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include sm {
      padding: 30px; }
    span {
      color: $white;
      @include bigText;
      display: inline-block;
      vertical-align: middle; }
    .icon {
      font-size: 23px;
      color: $white;
      display: inline-block;
      vertical-align: middle; } } }
