[data-tabs] {
	overflow: hidden;
	[data-tabs-control] {
		cursor: pointer;

		&[disabled],
		&.is-disabled {
			// cursor: not-allowed
 } }			// +nosel
	[data-tabs-wrapper] {
		position: relative; }
	[data-tabs-container] {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		opacity: 0;
		visibility: hidden;
		transition: opacity 0.3s, visibility 0.3s;
		&.is-open {
			position: relative;
			opacity: 1;
			visibility: visible; } } }

[data-tabs-current] {
  display: none; }

.tabs {
  &__list {
    display: flex;
    flex-direction: column;
    .tabs_bg & {
      flex-direction: row;
      justify-content: center; }
    @include sm {
      flex-direction: row;
      justify-content: space-around;
      .tabs_bg & {
        flex-direction: column; } } }
  &__item {
    display: block;
    color: $violet;
    @include bigText;
    .tabs_bg & {
      color: $mainTextColor;
      margin: 0 6px; }
    &:not(:last-child) {
      margin-bottom: 18px;
      .tabs_bg & {
        margin-bottom: 0;
        @include sm {
          margin-bottom: 10px; } } }
    &.is-active {
      span:after {
        @include fadein; }
      .tabs_bg & {
        background-image: $grad;
        color: $white; } }
    span {
      display: inline-block;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        height: 2px;
        width: 100%;
        background-color: $violet;
        @include fadeout(0.4s);
        .tabs_bg & {
          display: none; } } }
    .tabs_bg & {
      padding: 10px 15px 8px 15px;
      background-color: $light; } }
  &__wrapper {
    max-width: 518px;
    margin: 0 auto;
    margin-top: 30px; } }
